<template>
  <!-- 预约列表 -->
    <div class="home">
    <div class="demo-form-inline">
      <el-form :inline="true" :model="searchForm" class="demo-form-inline">
        <el-form-item label="关键字">
          <el-input v-model="searchForm.key" placeholder="档案好/姓名/手机号" />
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getSearchBtn()">查 询</el-button>
          <el-button type="primary" icon="el-icon-plus" @click="onSubmit">新 增</el-button
          >
        </el-form-item>
      </el-form>
    </div>
    <el-table :data="tableData" style="width: 100%">
      <el-table-column type="selection" width="55"> </el-table-column>
      <el-table-column prop="appointTime" label="预约时间"></el-table-column>
      <el-table-column prop="id" label="档案号"></el-table-column>
      <el-table-column prop="name" label="姓名"></el-table-column>
      <el-table-column prop="phone" label="手机号"></el-table-column>
      <el-table-column prop="age" label="年龄"></el-table-column>
      <el-table-column prop="packageTime" label="套餐名称"></el-table-column>
      <el-table-column label="操作" width="150">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="editRow(scope.row)"
            >编辑</el-button
          >
          <el-button type="text" size="small" @click="deleteTable(scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <!--分页表单-->
    <el-pagination
      class="mt-20p"
      background
      :current-page="searchForm.page"
      :page-size="searchForm.size"
      layout="->, total, prev, pager, next"
      :total="total"
      @current-change="handleCurrentChange"/>

  <!-- <AppointListDialog ref="AppointListDialog"></AppointListDialog>       -->
  </div>

</template>

<script>
// import AppointListDialog from '../../components/PhysicalAppointDialog/AppointListDialog'
import servicesApi from '../../services/servicesApi';

export default {
  name: "AppointList",
  mounted(){
    this.getAppointList();
  },
  // components: {
  //   AppointListDialog
  // },
  data() {
    return {
      searchForm: {
        // 查询条件
        key: "",
        page: 1,
        size: 10,
      },
      tableData: [],
      total: 13,
    };
  },
  methods:{
    //获取数据 
    getAppointList(){
      servicesApi.getAppointlistListApi().then((res)=>{
        this.tableData = res.data.data;
        console.log(res);
      })
    },
    //删除数据 
      deleteTable(row) {
        servicesApi.deleteAppointlistListApi(row.id).then((res)=>{
         this.getAppointList();
         console.log(res);
      })
    },

    //新增数据 
    onSubmit(){
      this.$refs.AppointListDialog.show()
    },

    //编辑数据 
    editRow(row) {
      this.$refs.AppointListDialog.show(row)
    },






    //分页
    handleCurrentChange() {

    },
    // 编辑表格

    // 搜索
    getSearchBtn() {},
    // 新增

  },
};
</script>

